.pcHome {
    .nav {
        position: fixed;
        z-index: 100;
        height: 8vh;
        width: 95%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 10px 30px;

        .logo {
            color: #fff;
            font-family: "AlphaCentauri";
            font-weight: 600;
            font-size: #{"max(2.5vh, 13px)"};
        }

        .navlinks ul {
            display: flex;
            flex-direction: row;
            li {
                padding: 0 #{"max(1vh, 4px)"};
                list-style: none;
                img {
                    width: #{"max(2.5vh, 13px)"};
                    height: #{"max(2.5vh, 13px)"};
                }
            }
            li:last-child{
                padding-right: #{"max(3vh, 20px)"};
            }
        }
    }

    section {
        height: 100vh;
        overflow: hidden;
        .bg {
            width: 110vw;
            height: 110vh;
            background-position: center;
            background-size: cover;
            filter: brightness(0.7);
        }

        .rock {
            width: 100vw;
            height: 100vh;
            img {
                position: absolute;
                width: #{"min(max(15vw, 25px), 15vh)"};
                left: 10vw;
                top: 20vh;
                filter: brightness(0.65);
            }
        }


        .text h1 {
            font-family: "AlphaCentauri";
            font-size: #{"max(5vw, 25px)"};
            color: #fff;
            letter-spacing: 20px;
            position: absolute;
            top: 35vh;
            left: 34vw;
        }

        .earth img {
            width: #{"max(35vw, 260px)"};
            height: #{"max(35vw, 260px)"};
            position: absolute;
            left: 34vw;
            top: 25vh;
            z-index: 1;
            filter: brightness(0.7);
        }

        .mid {
            left: -150px !important;
            bottom: -100px !important;
            top: unset !important;
            img {
                width: 100%;
                z-index: 2;
                left: -20px;
                top: 20vh !important;
                filter: brightness(0.8);
            }
        }

        .fore {
            right: -50px !important;
            bottom: -200px !important;
            top: 0px !important;
            left: unset !important;
            img {
                width: 400px;
                height: 110vh;
                z-index: 3;
                filter: brightness(0.5);
            }
        }
    }
}

.mobileHome {
    .title {
        color: #fff;
    }
}
